<template>
  <form class="register-form pure-form pure-form-stacked" @submit.prevent="submit">
    <label for="email" class="text-light">Email</label>
    <input type="email" id="email" name="email" required>
    <label for="username" class="text-light">Username</label>
    <input type="text" id="username" name="username" required>
    <label for="password" class="text-light">Password</label>
    <input type="password" id="password" name="password" required>
    <label for="password2" class="text-light">Repeat Passowrd</label>
    <input type="password" id="password2" name="password2" required>
    <div class="flex-column">
      <button class="ml-auto pure-button pure-button-primary" type="submit" :disabled="flag">Register</button>
      <small class="ml-auto text-light">
        already have account?
        <router-link class="link" to="/login">Login</router-link>
      </small>
    </div>
  </form>
</template>

<script>
export default {
  data() {
    return {
      flag: false,
    };
  },
  methods: {
    submit(event) {
      if (this.flag) {
        return;
      }
      const form = event.target;
      const formData = new FormData(form);
      if (!this.checkPassword(formData)) {
        this.$alert('error', 'Two password not matched.');
        return;
      }
      this.flag = true;
      this.$axios.post(this.$url.users, formData)
        .then(() => {
          this.$router.push('/login');
          this.flag = false;
        })
        .catch(() => {
          this.flag = false;
        });
    },
    checkPassword(formData) {
      const password1 = formData.get('password');
      const password2 = formData.get('password2');
      return password1 === password2;
    },
  },
};
</script>

<style lang="scss" scoped>
.register-form {
  width: 250px;
  display: inline-block;
  position: relative;
  margin: 5% auto;
  input {
    width: 100%;
  }
}
</style>
